:root{
--mainBlue:#2a2a72;
--lightBlue:#009ffd;
--mainWhite:#f3f3f3;
--mainDark:#232528;
--mainYellow:#ffa400

}


body{
  font-family: "Oswald",sans-serif !important;
background: var(--mainWhite) !important;
color: var(--mainDark) !important;

}
.text-title{
  font-family:"Permanent Mark", cursive;
  letter-spacing: .3rem;
  text-transform: uppercase;
}
.text-blue{
  color:(var(--mainDark)) !important;

}

.text-bright{
  color:var(--lightBlue) !important;
}
.btn-black{
  background:transparent;
  text-transform: capitalize;
  font-size: .8rem !important;
  color:var(--mainDark);
  border-radius: 0 !important;
  border:.1rem solid var(--mainDark) !important;
}
.btn-black:hover{
  cursor: pointer;
  font-size: 1.2rem;
  color:var(--mainYellow)

}